import React, { useEffect } from 'react'
import { useState } from 'react'
import MemberCard from '../components/MemberCard'
import AudioComp from '../components/AudioComp'
import EsLinks from '../components/EsLinks'
import MainAPI from '../assets/APIs/mainApi'
import SideImg from './SideImg'
// Images
import img from '../assets/img/chairman-sir1.webp'
import img_1 from "../assets/img/National-Helpline.jpg"
import img_2 from "../assets/img/mask-bd-portal-.jpg"
import axios from 'axios'
import Skli_MemberCard from './skeletonComp/Skli_Member'
import { SkeletonTheme } from 'react-loading-skeleton'
import Skli_EsLinks from './skeletonComp/Skli_EsList'

function SideBar() {
    const [mainData, setMainData] = useState(MainAPI.Straps);
    const updateList = mainData.filter(e => {
        return e.isSidebar === true
    })
    const API = `https://zpr.s3cbd.com/data/staff`
    const [staff, setStaff] = useState([])
    const [isLoading, setIsLoading] = useState(true)
    const getAPIData = async (api) => {
        try {
            const res = await axios.get(api)
            const Data = await res.data
            setStaff(Data)
            setIsLoading(false)
        } catch (error) {
            console.log(error.message);
        }
    }
    useEffect(() => {
        getAPIData(API)
    }, []);


    return (
        <>
            <div className="sideBar">
                <SkeletonTheme>
                    {
                        isLoading
                            ?
                            <div>
                                <Skli_MemberCard />
                                <br />
                                <Skli_MemberCard />
                                <br />
                                <Skli_EsLinks />
                            </div>
                            :
                            <div>
                                <div className="memberBox">

                                    {
                                        staff.map(e => {
                                            const { id, name, designation, image } = e
                                            const imgURL = `https://zpr.s3cbd.com/assets/zprstaff/`
                                            return (
                                                <>
                                                    <MemberCard id={id} tittle={designation} img={imgURL + image} reg={name} />
                                                </>
                                            )
                                        })
                                    }
                                </div>
                                <AudioComp tittle={'জাতীয় সংগীত'} />
                                <SideImg tittle={'মাস্ক পরুন সেবা নিন'} img={img_2} />
                                <SideImg tittle={'জরুরি হটলাইন'} img={img_1} />
                                <EsLinks />
                            </div>
                    }
                </SkeletonTheme>
            </div>
        </>
    )
}

export default SideBar
